'use client';

import { useState } from 'react';
import { FileUpload } from '../file-upload';
import CloudWatchLogs from '../components/cloudwatch-logs';

interface ParseResults {
  parsed_data: any;
  deduplication_status: 'created_new' | 'found_existing';
  content_hash: string;
  vector_ids: string[];
}

interface JobResponse {
  job_id: string;
  status: 'processing';
  total_chunks: number;
  message: string;
}

interface GridParserDashboardProps {
  userId: string;
}

export function GridParserDashboard({ userId }: GridParserDashboardProps) {
  const [isLoading, setIsLoading] = useState(false);
  const [results, setResults] = useState<ParseResults | null>(null);
  const [error, setError] = useState<string>('');
  const [currentFileName, setCurrentFileName] = useState<string>('');
  const [successMessage, setSuccessMessage] = useState<string>('');
  const [jobId, setJobId] = useState<string | null>(null);
  const [jobProgress, setJobProgress] = useState<{ current: number; total: number } | null>(null);

  // Function to poll job status
  const pollJobStatus = async (jobId: string) => {
    const maxAttempts = 120; // 10 minutes max (5-second intervals)
    let attempts = 0;

    const poll = async (): Promise<void> => {
      try {
        const response = await fetch(`/api/jobs/${jobId}`);
        if (!response.ok) {
          throw new Error(`Job status check failed: ${response.status}`);
        }

        const jobStatus = await response.json();
        console.log('Job status:', jobStatus);
        console.log('Job result:', jobStatus.result);

        // Update progress
        setJobProgress({
          current: jobStatus.progress.current,
          total: jobStatus.progress.total
        });

        if (jobStatus.status === 'completed') {
          // Job finished successfully
          setJobId(null);
          setJobProgress(null);
          setIsLoading(false);
          
          if (jobStatus.result) {
            setResults(jobStatus.result);
            setSuccessMessage('File processed successfully via async processing!');
          } else {
            setError('Job completed but no results found');
          }
          return;
        } else if (jobStatus.status === 'failed') {
          // Job failed
          setJobId(null);
          setJobProgress(null);
          setIsLoading(false);
          setError(jobStatus.error_message || 'Processing failed');
          return;
        } else if (jobStatus.status === 'processing' || jobStatus.status === 'pending') {
          // Job still running, continue polling
          attempts++;
          if (attempts < maxAttempts) {
            setTimeout(poll, 5000); // Poll every 5 seconds
          } else {
            setError('Processing timed out');
            setIsLoading(false);
            setJobId(null);
            setJobProgress(null);
          }
        }
      } catch (error) {
        console.error('Error polling job status:', error);
        attempts++;
        if (attempts < maxAttempts) {
          setTimeout(poll, 5000);
        } else {
          setError('Failed to check job status');
          setIsLoading(false);
          setJobId(null);
          setJobProgress(null);
        }
      }
    };

    // Start polling
    poll();
  };

  const handleFileSelect = async (file: File) => {
    setIsLoading(true);
    setError('');
    setResults(null);
    setSuccessMessage('');
    setCurrentFileName(file.name);

    try {
      // Read file content
      const fileContent = await readFileAsText(file);
      
      // Call backend API
      const apiUrl = process.env.NEXT_PUBLIC_API_URL || 'https://gdp2ynbdpc.execute-api.us-west-2.amazonaws.com/prod';
      const response = await fetch(`${apiUrl}/parse`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          fileContent: fileContent
        }),
      });

      if (!response.ok) {
        throw new Error(`API error: ${response.status} ${response.statusText}`);
      }

      const responseData = await response.json();
      
      // Check if this is an async job response
      if ('job_id' in responseData) {
        const jobResponse = responseData as JobResponse;
        setJobId(jobResponse.job_id);
        setJobProgress({ current: 0, total: jobResponse.total_chunks });
        setSuccessMessage(`Processing started with ${jobResponse.total_chunks} chunks. Polling for progress...`);
        
        // Start polling for job completion
        pollJobStatus(jobResponse.job_id);
      } else {
        // Synchronous response with direct results
        const parseResults = responseData as ParseResults;
        setResults(parseResults);
        setIsLoading(false);
      }

      // Save audit record to database (only for synchronous responses)
      if (!('job_id' in responseData)) {
        const parseResults = responseData as ParseResults;
        try {
          const auditResponse = await fetch('/api/audit', {
            method: 'POST',
            headers: {
              'Content-Type': 'application/json',
            },
            body: JSON.stringify({
              userId,
              fileName: file.name,
              parsedJson: parseResults.parsed_data,
              vectorId: parseResults.vector_ids?.[0] || 'unknown',
              status: 'success'
            }),
          });

          if (auditResponse.ok) {
            setSuccessMessage('File parsed successfully and saved to audit log!');
          } else {
            setSuccessMessage('File parsed successfully, but failed to save audit log.');
          }
        } catch (dbError) {
          console.error('Failed to save audit record:', dbError);
          setSuccessMessage('File parsed successfully, but failed to save audit log.');
        }
      }

    } catch (err) {
      const errorMessage = err instanceof Error ? err.message : 'Failed to parse file';
      setError(errorMessage);
      
      // Save error audit record
      try {
        await fetch('/api/audit', {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
          },
          body: JSON.stringify({
            userId,
            fileName: file.name,
            parsedJson: null,
            vectorId: 'error',
            status: 'error'
          }),
        });
      } catch (dbError) {
        console.error('Failed to save error audit record:', dbError);
      }
    } finally {
      setIsLoading(false);
    }
  };

  const readFileAsText = (file: File): Promise<string> => {
    return new Promise((resolve, reject) => {
      const reader = new FileReader();
      reader.onload = (e) => {
        if (e.target?.result) {
          resolve(e.target.result as string);
        } else {
          reject(new Error('Failed to read file'));
        }
      };
      reader.onerror = () => reject(new Error('Failed to read file'));
      reader.readAsText(file);
    });
  };

  return (
    <div className="h-screen flex gap-4 p-4">
      {/* Left Column - CloudWatch Logs (70%) */}
      <div className="w-[70%] h-full">
        <CloudWatchLogs 
          isActive={isLoading || results !== null}
        />
      </div>

      {/* Right Column - Dashboard Controls (30%) */}
      <div className="w-[30%] h-full overflow-y-auto space-y-6">
        {/* File Upload Section */}
        <div className="bg-white rounded-lg shadow p-6">
          <h2 className="text-lg font-medium text-gray-900 mb-4">
            Upload Grid Model File
          </h2>
          <p className="text-sm text-gray-600 mb-4">
            Upload a PSLF .raw file (up to 100MB) to extract structured data using AI-powered parsing.
          </p>
          <div className="mb-6 p-3 bg-blue-50 border border-blue-200 rounded-md">
            <p className="text-sm text-blue-700">
              <span className="font-medium">⏱️ Processing Time:</span> The Lambda processing typically takes 5-6 minutes for sample files. 
              Watch the live logs on the left for real-time progress updates.
            </p>
          </div>
          
          <FileUpload
            onFileSelect={handleFileSelect}
            isLoading={isLoading}
            accept=".raw"
            maxSize={100}
          />
          
          {successMessage && (
            <div className="mt-4 p-3 bg-green-50 border border-green-200 rounded-md">
              <p className="text-sm text-green-600">{successMessage}</p>
            </div>
          )}
        </div>

        {/* Processing Status */}
        {isLoading && (
          <div className="bg-white rounded-lg shadow p-6">
            <div className="flex items-center space-x-3">
              <svg
                className="animate-spin h-5 w-5 text-blue-500 flex-shrink-0"
                xmlns="http://www.w3.org/2000/svg"
                fill="none"
                viewBox="0 0 24 24"
              >
                <circle
                  className="opacity-25"
                  cx="12"
                  cy="12"
                  r="10"
                  stroke="currentColor"
                  strokeWidth="4"
                />
                <path
                  className="opacity-75"
                  fill="currentColor"
                  d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4z"
                />
              </svg>
              <div className="text-sm text-gray-600 min-w-0">
                <p className="font-medium truncate">
                  {jobId ? `Processing ${currentFileName} (async)` : `Processing ${currentFileName}`}
                </p>
                <p className="text-xs text-gray-500 mt-1">
                  {jobId ? 'S3-triggered background processing' : 'Direct AI processing'}
                </p>
                {jobId && (
                  <div className="mt-3">
                    <span className="text-xs text-gray-500 block truncate">Job: {jobId.substring(0, 8)}...</span>
                    <div className="mt-2 p-2 bg-yellow-50 border border-yellow-200 rounded text-xs">
                      <span className="text-yellow-700">
                        ⏱️ Expected processing time: 5-6 minutes for typical files
                      </span>
                    </div>
                    {jobProgress && (
                      <div className="mt-2">
                        <div className="bg-gray-200 rounded-full h-2">
                          <div
                            className="bg-blue-500 h-2 rounded-full transition-all duration-300"
                            style={{
                              width: `${Math.round((jobProgress.current / jobProgress.total) * 100)}%`
                            }}
                          />
                        </div>
                        <span className="text-xs text-gray-500 mt-1 block">
                          {jobProgress.current}/{jobProgress.total} chunks ({Math.round((jobProgress.current / jobProgress.total) * 100)}%)
                        </span>
                      </div>
                    )}
                  </div>
                )}
              </div>
            </div>
          </div>
        )}

        {/* Results Section */}


        {/* Info Section */}
        <div className="bg-blue-50 border border-blue-200 rounded-lg p-6">
          <h3 className="text-sm font-medium text-blue-900 mb-2">
            About This Tool
          </h3>
          <div className="text-sm text-blue-700 space-y-2">
            <p>
              AI-powered parsing using Claude 3.7 Sonnet with S3-triggered processing for large files.
            </p>
            <ul className="list-disc list-inside space-y-1 text-xs">
              <li>Real-time CloudWatch logs streaming</li>
              <li>Automatic chunking for 100MB+ files</li>
              <li>Background processing with progress tracking</li>
              <li>Vector embeddings for semantic search</li>
            </ul>
          </div>
        </div>
      </div>
    </div>
  );
}